.container {
  display: flex;
  height: 100vh;
  padding: 50px 0;
}

.section {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  overflow: hidden;
  background-size: cover;
  background-position: center;
  color: #fff;
  transition: flex 0.4s ease-in;
  position: relative;
}

.section .overlay {
  /* background-color: rgba(0, 0, 0, 0.4); */
  width: 100%;
  height: 100%;
  position: absolute;
  transition: background-color 1s linear;
}

.section .content {
  z-index: 2;
  font-size: 60%;
}

/* .section:hover {
  flex: 4;
} */

/* .section:hover .content {
  align-items: flex-start;
} */

.section:hover .content {
  font-size: 80%;
  transition: ease-in-out 0.4s;
}

@media (max-width: 768px) {
  .container {
    display: flex;
    flex-direction: column;
  }

  .section {
    align-items: flex-start;
  }

  .section:hover {
    flex: 8;
  }

  .section .content {
    margin-top: 20px;
  }
}
