.about__wrapper {
  margin: 0 10%;
  border-radius: 6px;
  padding: 10px;
  background-color: white;
  box-shadow: 3px 3px 5px 4px rgba(0, 0, 0, 0.84);
  line-height: 1.5;
}

.about__paragraph {
  padding: 2%;
  font-size: 130%;
  line-height: 1.5;
}

.about__paragraph:first-of-type {
  padding: 10px;
  font-weight: 600;
  text-align: center;
  font-size: 150%;
  line-height: 1.5;
}

@media (max-width: 768px) {
  .about__wrapper {
    margin: 0 5%;
  }

  .about__paragraph:first-of-type {
    padding: 5px;
    font-weight: 600;
    text-align: center;
    font-size: 100%;
    line-height: 1.5;
  }

  .about__paragraph {
    font-size: 90%;
  }
}
