.projectItem__wrapper {
  text-decoration: none;
  color: #0a252c;
  display: flex;
  flex-direction: row;
  padding: 1%;
  cursor: pointer;
}

.projectItem__img {
  border: 1px black solid;
  width: 15%;
  min-width: 120px;
  max-height: 75px;
}

.projectItem__paragraph {
  width: 100%;
  padding: 10px;
}

@media (max-width: 768px) {
  .projectItem__paragraph {
    font-size: 70%;
    line-height: 1.2;
    padding: 10px;
  }

  .projectItem__img {
    min-width: 120px;
    height: auto;
    max-height: 75px;
    margin-top: 10px;
    border: 1px black solid;
  }
}
