.toolbar {
    background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
    width: 100%;
    display: inline-flex;
    position: fixed;
    float: left;
    height: 50px;
    color: white;
    z-index: 999;
    border-bottom: 1px solid rgba(0, 0, 0, .7);
}

.toolbar__navigation {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
    z-index: 999;
}

.toolbar__logo {
    height: 100%;
    padding: 3px 4px;
}

.toolbar__logo-image {
    height: 100%;
}

.toolbar__logo a {
    color: black;
    text-decoration: none;
    font-size: 1.5rem;
    z-index: 999;
}

.spacer {
    flex: 1;
    z-index: 200;
}

.toolbar__navigation ul {
    list-style: none;
    display: flex;
}

.toolbar__navigation li {
    margin: 0 0.5rem;
    color: black;
}

.toolbar__navigation-items a {
    color: white;
    text-decoration: none;
}

.toolbar__navigation-items a:hover,
.toolbar__navigation-items a:active {
    color: red;
}