.blurb__wrapper {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  color: black;
}

.blurb__title {
  text-align: center;
  font-size: 200%;
  max-width: 275px;
  margin: auto 15px 15px 15px;
  background: white;
  border-radius: 6px;
  padding: 8px;
  box-shadow: 3px 3px 5px 4px rgba(0, 0, 0, 0.84);
}

@media (max-width: 768px) {
  .blurb__title {
    margin: 1% auto 3% auto;
    font-size: 110%;
    min-width: 150px;
    padding: 5px;
    box-shadow: none;
    box-shadow: 3px 3px 5px 4px rgba(0, 0, 0, 0.84);
  }
}
