.projects__wrapper {
  margin: 0 10%;
  border-radius: 6px;
  padding: 10px;
  background-color: white;
  box-shadow: 3px 3px 5px 4px rgba(0, 0, 0, 0.84);
  line-height: 1.2;
}

.projects__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.projects__paragraph {
  padding: 1%;
  font-size: 100%;
  line-height: 1.5;
}

.Collapsible__trigger {
  box-sizing: border-box;
  display: block;
  font-size: 120%;
  font-weight: 600;
  text-decoration: none;
  color: #0a2c11;
  position: relative;
  border: 1px solid white;
  padding: 10px;
  background: white;
}

.Collapsible__trigger.is-open {
  background: #0a2c11;
  color: white;
}

.Collapsible__trigger:hover {
  background: #0a2c11;
  color: white;
}

.Collapsible__trigger:after {
  content: "<";
  position: absolute;
  right: 10px;
  top: 10px;
  display: block;
  transition: transform 300ms;
}
.Collapsible__trigger.is-open:after {
  transform: rotateZ(-90deg);
}
.Collapsible__trigger.is-disabled {
  opacity: 0.5;
  background-color: grey;
}

@media (max-width: 768px) {
  .projects__wrapper {
    margin: 0 5%;
  }

  .projects__paragraph {
    font-size: 70%;
  }

  .projects__wrapper .Collapsible__trigger {
    font-size: 75%;
    line-height: 1;
  }
}
