.skillItem__wrapper {
  min-width: 19%;
  font-weight: 600;
  padding: 5px;
  border-radius: 2px;
  color: #0a2c11;
  margin: 1%;
}

.skillItem__wrapper:hover {
  color: white;
  background-color: #0a2c11;
}

@media (max-width: 600px) {
  .skillItem__wrapper {
    font-size: 70%;
    font-weight: 600;
    padding: 1%;
    border-radius: 2px;
    color: #0a2c11;
    margin: 1%;
  }
}
