.section {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  overflow: hidden;
  background-size: cover;
  background-position: center;
  color: #fff;
  transition: flex 0.4s ease;
  position: relative;
}

.section__content {
  z-index: 2;
}

.section.expanded {
  flex: 5;
}

.section__overlay {
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  position: absolute;
  transition: background-color 1s ease;
}

.section__overlay.expanded {
  background-color: rgba(0, 0, 0, 0.1);
}

#aboutme {
  background: url(../../../assets/backgrounds/aboutme-bg.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  /* background: #de3163; */
}

#skills {
  background: url(../../../assets/backgrounds/skills-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  /* background: #31de55; */
}

#projects {
  background: url(../../../assets/backgrounds/projects-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  /* background: #31bade; */
}

@media (max-width: 768px) {
  .section {
    align-items: center;
  }

  .section.expanded {
    flex: 8;
  }

  .section__overlay {
    background-color: rgba(0, 0, 0, 0.6);
  }

  .section__overlay.expanded {
    background-color: rgba(0, 0, 0, 0.1);
  }

  #aboutme {
    background: #de3163;
    background: url(../../../assets/backgrounds/aboutme-bg.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
  }

  #skills {
    background: #31de55;
    background: url(../../../assets/backgrounds/skills-bg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
  }

  #projects {
    background: #31bade;
    background: url(../../../assets/backgrounds/projects-bg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
  }
}
