.skills__wrapper {
  margin: 0 10%;
  border-radius: 6px;
  padding: 10px;
  background-color: white;
  box-shadow: 3px 3px 5px 4px rgba(0, 0, 0, 0.84);
  line-height: 1.2;
}

.skills__paragraph {
  padding: 1%;
  font-size: 100%;
  line-height: 1.5;
}

.skills__addendum {
  padding: 1%;
  font-size: 70%;
  line-height: 1.5;
  float: right;
}

.skills__list {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  margin-top: 1%;
  justify-content: center;
  flex-direction: row;
}

.skills__wrapper .Collapsible__trigger {
  display: block;
  font-size: 120%;
  font-weight: 600;
  text-decoration: none;
  color: #0a2c11;
  position: relative;
  border: 1px solid white;
  padding: 10px;
  background: white;
}

.skills__wrapper .Collapsible__trigger.is-open {
  background: #0a2c11;
  color: white;
}

.skills__wrapper .Collapsible__trigger:hover {
  background: #0a2c11;
  color: white;
}

.skills__wrapper .Collapsible__trigger:after {
  content: "<";
  position: absolute;
  right: 10px;
  top: 10px;
  display: block;
  transition: transform 300ms;
}
.skills__wrapper .Collapsible__trigger.is-open:after {
  transform: rotateZ(-90deg);
}
.skills__wrapper .Collapsible__trigger.is-disabled {
  opacity: 0.5;
  background-color: grey;
}

@media (max-width: 768px) {
  .skills__wrapper {
    margin: 0 5%;
  }

  .skills__paragraph {
    font-size: 70%;
  }

  .skills__wrapper .Collapsible__trigger {
    font-size: 75%;
    line-height: 1;
  }
}
