.botnav {
  position: fixed;
  z-index: 999;
  bottom: 0;
  height: 50px;
  width: 100%;
  background-image: linear-gradient(to top, #e2ebf0 0%, #cfd9df 100%);
  border-top: 1px solid rgba(0, 0, 0, 0.7);
}

.botnav__nav {
  height: 100%;
  display: flex;
  flex-direction: row;
}

.botnav__nav-spacer {
  flex: 2;
}

.botnav__nav-links {
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.botnav__nav-icon a {
  height: 100%;
  width: 20px;
  font-size: 30px;
  padding: 10px;
  color: gray;
}
