.botnavItem {
  margin: auto 5%;
}

.botnavItem a {
  height: 100%;
  width: 20px;
  font-size: 30px;
  padding: 10px;
  color: gray;
  margin: 5%;
}

.facebook:hover {
  color: #3b5998;
  transition: 0.4s;
}

.messenger:hover {
  color: #3b5998;
  transition: 0.4s;
}

.linkedin:hover {
  color: #00a0dc;
  transition: 0.4s;
}

.mail:hover {
  color: black;
  transition: 0.4s;
}

.github:hover {
  color: purple;
  transition: 0.4s;
}
